import React from "react";
import {
  Divider,
  Grid,
  Switch,
  Typography,
} from "@mui/material";
import HeaderWrapper from "components/Wrapper/HeaderWrapper";
import { makeStyles } from "@mui/styles";
import { ReactComponent as PushNotificationIcon } from "assets/PushNotificationIcon.svg";
import { ReactComponent as MainNotificationIcon } from "assets/MainNotificationIcon.svg";
import { ReactComponent as WhatsappNotificationIcon } from "assets/WhatsappNotificationIcon.svg";
import ArrowForwardRoundedIcon from '@mui/icons-material/ArrowForwardRounded';
import { useNavigate } from "react-router";

const useStyles = makeStyles((theme) => ({
  dmProfileRoot: {
    height: "100%",
    padding: theme.spacing(1),
    paddingTop: theme.spacing(2),
    "&>div": {
      marginTop: 10,
    },
  },
  dmNotificationContainer: {
    "&>div": {
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
      padding: "4px 0px",
      "&>div": {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        "&>svg": {
          margin: "0px 6px 0px 4px ",
          width: 20,
          height: 20,
        },
      },
    },
  },
}));


export default function Setting() {
  const classes = useStyles();
  const navigate = useNavigate()

  const redirectTo = (location) => () => {
    navigate(`/setting/${location}`)
  }

  return (
    <HeaderWrapper>
      <Grid className={classes.dmProfileRoot}>
        <Typography variant="h5">Setting</Typography>
        <Grid className={classes.dmNotificationContainer}>
          <Grid onClick={redirectTo("account")}>
            <Grid>
              <Typography semiBold variant="subtitle1">
                Account
              </Typography>
            </Grid>
            <ArrowForwardRoundedIcon />
          </Grid>
          <Divider />

          <Grid onClick={redirectTo("group")}>
            <Grid>
              <Typography semiBold variant="subtitle1">
                Group
              </Typography>
            </Grid>
            <ArrowForwardRoundedIcon />
          </Grid>
          <Divider />
          <Grid onClick={redirectTo("users")}>
            <Grid>
              <Typography semiBold variant="subtitle1">
                Event Users
              </Typography>
            </Grid>
            <ArrowForwardRoundedIcon />
          </Grid>
          <Divider />
        </Grid>

        <Grid className="comming-soon">
          <Typography grey variant="body2">
            Notification
          </Typography>
          <Divider />
          <Grid className={classes.dmNotificationContainer}>
            <Grid>
              <Grid>
                <Typography semiBold variant="subtitle1">
                  Reminder
                </Typography>
              </Grid>
              <Switch />
            </Grid>
            <Divider />
            <Grid>
              <Grid>
                <PushNotificationIcon />
                <Typography semiBold variant="subtitle1">
                  Push notification
                </Typography>
              </Grid>
              <Switch />
            </Grid>
            <Divider />
            <Grid>
              <Grid>
                <MainNotificationIcon />
                <Typography semiBold variant="subtitle1">
                  Mail
                </Typography>
              </Grid>
              <Switch />
            </Grid>
            <Divider />
            <Grid>
              <Grid>
                <WhatsappNotificationIcon />
                <Typography semiBold variant="subtitle1">
                  Whatsapp
                </Typography>
              </Grid>
              <Switch />
            </Grid>
            <Divider />
          </Grid>
        </Grid>

        <Grid className="comming-soon">
          <Typography grey variant="body2">
            Other thing
          </Typography>
          <Divider />
          <Grid className={classes.dmNotificationContainer}>
            <Grid>
              <Grid>
                <Typography semiBold variant="subtitle1">
                  Remind me for events
                </Typography>
              </Grid>
              <Switch />
            </Grid>
            <Divider />
            <Grid>
              <Grid>
                <Typography semiBold variant="subtitle1">
                  Dark mode
                </Typography>
              </Grid>
              <Switch />
            </Grid>
            <Divider />
          </Grid>
        </Grid>
      </Grid>
    </HeaderWrapper>
  );
}
